@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body {
    height: 100%;
    width: 100%;
    background-color: #fff;
    font-family: "Quicksand", sans-serif;
}

#table tr:nth-child(even){background-color: rgb(0,0,0, 0.1); }

@media screen and (min-width: 1024px) {
    #root {
        height: 100%;
        padding: 0 25%;
    }

    .container {
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        z-index: 0;
        background-color: #ffdde1;
    }

    section{
        padding: 5%;
    }
    
    .big-logo{
        padding: 5%;
    }

    .big-logo img{
       width: 100%;
    }
}

@media screen and (min-width: 481px) and (max-width: 1024px) {
    #root {
        height: 100%;
        padding: 0 15%;
    }

    .container {
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        z-index: 0;
        background-color: #ffdde1;
    }

    section{
        padding: 5%;
    }
    
    .big-logo{
        padding: 5%;
    }

    .big-logo img{
       width: 100%;
    }
}

@media (max-width: 480px) {
    #root {
        height: 100%;
    }

    .container {
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        z-index: 0;
        background-color: #ffdde1;
    }
    
    section{
        padding: 5%;
    }
    .big-logo{
        padding: 5%;
    }

    .big-logo img{
       width: 100%;
    }
}

.navbar-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    background: #ee9ca7; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(
        to bottom,
        #ffdde1,
        #ee9ca7
    ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.navbar-container .item {
    width: 33.3%;
    padding: 4%;
}

.navbar-container .item img {
    width: 100%;
}

.navbar-container .item i {
   font-size: 2rem;
}

.navbar-container .item span {
   float: right;
   font-size: 1.2rem;
 }

.navbar-container .item i:hover {
    cursor: pointer;
    transform: scale(1.1);
 }
 

.btn{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1rem;
    padding:5% 0 5% 0;
    background-color: #ee9ca7;
    border-radius: 8px;
    border: 3px solid #fff;
    box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
    transition: linear 0.1s;
}


.btn:hover{
    cursor: pointer;
    transform: scale(1.03);
}

.section-container{
    background-color: #fff;
    border-radius: 8px;
    box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
    padding:5% 5% 5% 5%;
    line-height: 150%;
}

.section-container span{
    line-height: 80%;
}
.table-container{
    border-radius: 8px;

}

table{
    width: 100%;
    color: #fff;
    border-collapse: collapse;
}

table tr td{
    padding: 3%;
    border-top: none;
    border-bottom: 2px solid #ffdde1; 
}

table tr td .content{
    line-height: 150%;
    padding: 4%;
}

table tr td .first{
  font-size: 1.4rem;
  font-weight: bold;
  text-align: center;
}

.br-bottom-8{
    border-radius: 0px 0px 8px 8px;
}

.br-top-8{
    border-radius: 8px 8px 0px 0px;
}

.title{
    padding: 5%;
}

.price{
    width: 20%;
    padding: 2%;
}

.quota{
    width: 21%;
    padding: 2%;
}

.bg-white{
    background-color: #fff;
    color: black;
}

.bg-amour{
    background-color: #ee5253;
}

.bg-pastel{
    background-color: #ff6b6b;
}

.bg-nasu{
    background-color: #5f27cd;
}

.bg-bluebell{
    background-color: #341f97;
}

.bg-imperial{
    background-color: #222f3e;
}
.footer-container{
  width: 100%;
  text-align: center;
}
